import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import {IonicStorageModule} from '@ionic/storage';
import { FormsModule } from '@angular/forms';

import { MyApp } from './app.component';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { GoogleMaps } from "@ionic-native/google-maps";
//import { formArrayNameProvider } from '@angular/forms/src/directives/reactive_directives/form_group_name';

import {AuthProvider} from '../providers/auth/auth';
import {UserStateProvider} from "../providers/userstate/user-state";
import { GenericProvider } from '../providers/generic/GenericProvider';
import {HttpClientModule} from "@angular/common/http";
import { Geolocation} from '@ionic-native/geolocation'
import { autocompletePageModule } from '../pages/autocomplete/autocomplete.module';
import { LocationAccuracy } from '@ionic-native/location-accuracy';
import { LocationsProvider } from '../providers/Map/locations';
import { Diagnostic } from '@ionic-native/diagnostic';
import { OpenNativeSettings } from '@ionic-native/open-native-settings';
import {BackgroundGeolocation} from '@ionic-native/background-geolocation'
import { QRScanner } from '@ionic-native/qr-scanner';
@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/before-scan/before-scan.module.ngfactory#BeforeScanPageModuleNgFactory', name: 'BeforeScanPage', segment: 'before-scan', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mapsapi/mapsapi.module.ngfactory#MapsapiPageModuleNgFactory', name: 'MapsapiPage', segment: 'mapsapi', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/scan-code/scan-code.module.ngfactory#ScanCodePageModuleNgFactory', name: 'ScanCodePage', segment: 'scan-code', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/maps/maps.module.ngfactory#MapsPageModuleNgFactory', name: 'MapsPage', segment: 'maps', priority: 'low', defaultHistory: [] }
  ]
}),
    autocompletePageModule,
    IonicStorageModule.forRoot({
      name: '__mydb',
      driverOrder: ['indexeddb', 'sqlite', 'websql','localStorage']    //,localStorage
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    GoogleMaps,
    AuthProvider,
    UserStateProvider,
    GenericProvider,
    Geolocation,
    LocationAccuracy,
    Diagnostic,
    OpenNativeSettings,
    BackgroundGeolocation,
    LocationsProvider,
    QRScanner,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



